    <div class="city-word">
        <ul>
            <li @click="handleClick"  v-for="(item,index) of cities" :key="index" >{{index}}</li>
        </ul>
    </div>
</template>
<script>
export default {
  name: "CityWord",
  props: ["cities"],
  methods: {
    handleClick(event) {
