export default {
    changeLetter(state, letter) {
        state.letter = letter
    },
    changeCity(state, city) {
        // 一般网络请求或缓存用try，catch包裹，容错率加大
        try {
            localStorage.setItem("city", city)
        } catch (err) {
            console.log(err)
        }
        state.city = city
    }
}